.develop-uxui .content-container {
	position: relative;
	display: flex;
	flex-direction: column;
	justify-content: center;
}
.develop-uxui__title {
	padding-top: 72px;
	text-align: center;

	font-family: 'MaruBuri';
	font-style: normal;
	font-weight: 700;
	font-size: 32px;
	line-height: 43px;

	letter-spacing: -0.015em;

	color: #212529;
}
.develop-uxui__button--closing {
	position: absolute;
	width: 48px;
	height: 48px;
	right: 20vw;
	top: 70px;
	cursor: pointer;
}
.develop-uxui__headtext-develop {
	margin-top: 70px;
	margin-bottom: 29px;
	text-align: center;
	font-family: 'MaruBuri';
	font-style: normal;
	font-weight: 700;
	font-size: 24px;
	line-height: 32px;
	letter-spacing: -0.015em;

	color: #212529;
}
.develop-uxui__develop-content-wrapper-develop {
	display: flex;
	justify-content: center;
	gap: 30px;
	flex-wrap: wrap;
}
.develop-uxui__card-wrapper {
	flex-direction: column;
	width: 460px;
	height: 490px;
	background: #ffffff;
	border: 1px solid #dfdede;
	border-radius: 20px;
	margin-top: 41px;

	-webkit-filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));

	filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
}
.develop-uxui__card-link {
	display: block;
	text-align: center;
	margin-top: 25px;
	/* width: 401.03px; */
	/* margin-left: 29.5px; */
}
.no-link {
	margin-top: 25px;
	width: 401.03px;
	margin-left: 29.5px;
}
.develop-uxui__card-wrapper .develop-uxui__card-borderline {
	background-image: linear-gradient(
		to right,
		#ff2068 50%,
		rgba(255, 255, 255, 0) 0%
	);
	background-position: bottom;
	background-size: 16px 1px;
	background-repeat: repeat-x;
	padding-bottom: 14px;
	margin-bottom: 11px;
	width: 200.51px;
	margin-left: 29.5px;
}
.develop-uxui__card-wrapper .develop-uxui__card-title {
	font-family: 'MaruBuri';
	font-style: normal;
	font-weight: 600;
	font-size: 20px;
	line-height: 36px;
	margin-top: 15px;
	/* identical to box height, or 180% */

	letter-spacing: -0.015em;

	color: #3c3243;
	margin-left: 29.5px;
}
.develop-uxui__card-wrapper .develop-uxui__card-maintext {
	font-family: 'Noto Sans KR';
	font-style: normal;
	font-weight: 400;
	font-size: 18px;
	line-height: 160%;
	/* or 29px */

	letter-spacing: -0.015em;

	color: #3c3243;
	margin-left: 29.5px;
}

.develop-uxui__develop-content-wrapper-uxui {
	display: flex;
	justify-content: center;
	gap: 30px;
	padding-bottom: 70px;
	flex-wrap: wrap;
}

.develop-uxui__headtext-uxui {
	margin-top: 100px;
	margin-bottom: 9px;
	text-align: center;
	font-family: 'MaruBuri';
	font-style: normal;
	font-weight: 700;
	font-size: 24px;
	line-height: 32px;
	letter-spacing: -0.015em;

	color: #212529;
}
